<template>
  <div :class="[areaJude?'areaJude':'e-date-picker']">
    <e-input
            :class="{ areaJude: areaJude }"
      disabled
      isLink
      @click="showPicker = true"
      textAlign="left"
      v-model="currentText"
      :placeholder="placeholder"
    >
      <img class="input_icon icon_name" style="vertical-align: middle;"
      src=@/assets/svg/area.svg>
    </e-input>
    <van-popup v-model="showPicker" position="bottom">
      <van-area
        @cancel="cancel"
        @confirm="confirm"
        @change="areaChange"
        :area-list="areaList"
        v-bind="$attrs"
        v-on="$listeners"
        :columns-placeholder="
          type === '0' ? [] : ['请选择', '请选择', '请选择']
        "
      ></van-area>
    </van-popup>
  </div>
</template>

<script>
import util from "@/components/_utils/index.js";
import eInput from "@/components/input";
import loading from "@/components/loading/index.js";
import apis from "@/api/config.js";
import { mapState, mapMutations, mapGetters } from "vuex";
export default {
  name: "e-area",
  components: {
    eInput
  },
  data() {
    return {
      areaCode: "",
      currentValue: "",
      currentText: "",
      showPicker: false,
      currentCode: "",
      areaList: {
        city_list: {},
        county_list: {},
        province_list: {}
      },
      type: "",
      lv1BrNo: "",
      allareaPrdCode: "",
      prdId: "",
      provinceCode:''
    };
  },
  props: {
    placeholder: {
      type: String,
      default: "请选择"
    },
    placeholderColor: {
      type: String,
      default: "#4ca2fe"
    },
    readonly: {
      type: Boolean,
      default: false
    },
    text: {
      type: String,
      default: ""
    },
    value: {
      type: String,
      default: ""
    },
    cityCode: {
      type: String,
      default: ""
    },
    allarea: {
      type: Boolean,
      default: false,
    },
    areaJude: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapGetters({
      productData: "product"
    })
  },
  watch: {
    currentValue(val) {
      val = util.safeValue(val);
      this.currentText = val[0].name();
      this.currentText = val[1].name()
        ? this.currentText + '-' + val[1].name()
        : this.currentText;
      this.currentText = val[2].name()
        ? this.currentText + '-' + val[2].name()
        : this.currentText;
      this.$emit("input", val[2].code());
      this.areaCode = val[2].code();
      let areaObj = {
        areaText: this.currentText,
        netcode: this.lv1BrNo
      };
      this.$emit("netcode", areaObj);
    },
    value: {
      handler(val) {
        if (val) {
          this.showDefault(val);
        }
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    confirm(payLoad) {
      // console.log(payLoad,"payLoad");
      if (
        this.type !== "0" &&
        (!payLoad[2] || !payLoad[2].code || !payLoad[2].name)
      ) {
        this.$toast("请选择区信息");
        return;
      }
      this.currentValue = payLoad;
      this.showPicker = false;
    },
    cancel() {
      this.showPicker = false;
    },
    trans(data) {
      let res = {};
      let obj = {};
      let filterList = ['320500','370200','440300']
      data.forEach(item => {
          if(/0000/.test(item.areaCode)){
            if(item.areaCode === this.provinceCode.slice(0, 2) + "0000"){
              res[item.areaCode] = item.areaName;
            }
          }else{
              if(item.areaCode === this.cityCode&&filterList.includes(this.cityCode)){
                obj[item.areaCode] = item.areaName;
                return
              }
              if(filterList.includes(item.areaCode)){
                return
              }
            res[item.areaCode] = item.areaName;
        }
      });
      return Object.values(obj).length > 0 ? obj : res;
    },
    init() {
      if(!this.prdId) return
      return new Promise((resolve, reject) => {
        this.$api.common
          .PrdParentAreaList(
            {
              operFlag: "0",
              prdId:
                this.allarea && this.allareaPrdCode
                  ? this.allareaPrdCode
                  : this.prdId
            },
            false
          )
          .then(res => {
            loading.hide();
            if (res.status === "1") {
              this.areaList.province_list = this.trans(res.data);
            } else {
              reject(res.error);
            }
          });
      });
    },
    areaChange(components, value, index) {
      if (this.type !== "0" && index + 2 < 4) {
        let area = ["city_list", "county_list"];
        this.api.common
          .PrdChildAreaList(
            {
              areaLevel: index + 2,
              parentAreaCode: value[index].code,
              operFlag: "0",
              prdId: this.prdId
            },
            false
          )
          .then(res => {
            loading.hide();
            if (index == "0") {
              this.lv1BrNo = value[0].code;
              res.data.forEach(item => {
                if (item.singletonFlag == "1") {
                  this.lv1BrNo = item.areaCode;
                }
              });
            }
            if (res.status === "1") {
              this.areaList[area[index]] = this.trans(res.data);
            }
          });
      }
    },
    async showDefault(areaCode) {
      // 绑定一个完整的code反显省市区文字
      if (
        areaCode.length != 6 ||
        /000/.test(areaCode) ||
        areaCode == this.areaCode
      ){return;}
      let proviceCode = areaCode.slice(0, 2) + "0000";
      let cityCode = areaCode.slice(0, 4) + "00";
      if (!Object.keys(this.areaList.province_list).length) {
        console.log("111");
        await Promise.all[this.init()];
      }
      const city_listRes = await this.api.common.PrdChildAreaList({
        areaLevel: 2,
        parentAreaCode: proviceCode,
        operFlag: "0",
        prdId: this.prdId
      });
      const county_listRes = await this.api.common.PrdChildAreaList({
        areaLevel: 3,
        parentAreaCode: cityCode,
        operFlag: "0",
        prdId: this.prdId
      });
      loading.hide()
      this.lv1BrNo = proviceCode;
      if (city_listRes.status === "1") {
        city_listRes.data.forEach(item => {
          if (item.singletonFlag == "1") {
            this.lv1BrNo = item.areaCode;
          }
        });
        this.areaList.city_list = this.trans(city_listRes.data);
      }
      if (county_listRes.status === "1") {
        this.areaList.county_list = this.trans(county_listRes.data);
      }
      if (city_listRes.status === "1" && county_listRes.status === "1") {
        let lis = ["province_list", "city_list", "county_list"];
        let payLoad = [proviceCode, cityCode, areaCode].map((item, index) => {
          return { code: item, name: this.areaList[lis[index]][item] };
        });
        this.currentValue = payLoad;
      }
    }
  },
  async created() {
    var productObj = JSON.parse(sessionStorage.getItem("productData"));
    if (JSON.stringify(this.productData) == "{}") {
      this.prdId = productObj.prd_id;
    } else {
      this.prdId = this.productData.prd_id;
    }
    this.iconShow = !this.readonly;
    this.provinceCode = this.value
    if (this.allarea) {
      let res = await this.api.insured.getPrdId({ prdCode: "ALLIN" });
      if (res.status == 1) {
        this.allareaPrdCode = res.data;
      }
    }
    if (this.$attrs["area-list"] || this.$attrs["areaList"]) {
      this.type = "0";
      this.areaList = this.$attrs["area-list"] || this.$attrs["areaList"];
    } else {
      this.currentText = this.text;
      await Promise.all([this.init()]);
    }
    if (
      (this.$attrs["area-list"] || this.$attrs["areaList"]) &&
      this.$attrs.value
    ) {
      this.currentValue = util.getArea(
        this.$attrs["area-list"] || this.$attrs["areaList"],
        this.$attrs.value
      );
    }
  }
};
</script>

<style lang="less" scoped>
@fontSize: 16px;
@color: #303133;
.e-date-picker {
  position: relative;
  &::after {
    content: "";
    height: 1px;
    display: block;
    position: absolute;
    width: 100%;
    background-color: #efefef;
    bottom: 0;
    left: 0;
  }
  .e-date-picker-content {
    font-size: @fontSize;
    color: @color;
    display: flex;
    align-items: center;
    line-height: 20px;
    .unfold {
      transform: rotate(90deg);
    }
    .shrink {
      transform: rotate(0deg);
    }
    .unfold,
    .shrink {
      transform-origin: right center;
      -webkit-transform-origin: center center;
      transition: transform 0.3s;
      margin-left: 3.2vw;
    }
  }
}
.areaJude{
 /deep/ .e-input-com .e-input-view .e-inpit-title {
    width: 0 !important;
  }
  /deep/ .e-input-com .e-input-view .e-input[data-v-14765ded] {
    flex: 1;
    text-align: var(--align);
    /* text-align: left; */
    padding: 0 0 0 8%;
    box-sizing: border-box;
    outline: none;
    border: none;
}
}

</style>