<template>
  <div :class="['e-input-com',{'error-input-com':error}]">
    <div class="e-input-view" :style="{'--padding':padding}">
      <div class="e-inpit-title">
        <slot></slot>
      </div>
      <input
        :class="['e-input',{'e-input-error':error},className]"
        v-model="currentValue"
        v-if="!disabled && !email"
        :type="type"
        :style="{ '--color' : placeholderColor,'--align':textAlign}"
        :maxlength="maxlength"
        :placeholder="placeholder"
        ref="input"
        @input="inputValue"
        @focus="focusInput"
        @blur="blur"
        :readonly="readonly"
      >
      <div
        v-if="email"
        class="email_div e-input"
        @click="focusEmail"
        ref="emailInput"
        @keydown="noWrap"
        @blur="completeEmail"
        contenteditable="true"
        :data-tip="autoCompleteText"
        tabindex="2"
        :data-placeholder="emailPlaceholder"
        @focus="emailFocusInput"
        @input="autoComplete()"
      ></div>
      <div
        :class="['e-input',{div:isLink},]"
        v-if="disabled"
        @click="eventClick"
        :style="{ '--color' : placeholderColor ,'--borderColor':borderColor,'--align':textAlign}"
        :data-placeholder="_placeholder"
      >{{currentValue | filter(filterType)}}</div>
    </div>
  </div>
</template>

<script>
import mask from "vanilla-masker";
import props from "./props";
export default {
  name: "e-input",
  props,
  data() {
    return {
      currentValue: "",
      emailPlaceholder: "",
      autoCompleteText: ""
    };
  },
  computed: {
    _placeholder() {
      return this.currentValue ? "" : this.placeholder;
    },
    borderColor() {
      return this.value ? "#333" : this.placeholderColor;
    }
  },
  filters: {
    filter(value, type) {
      switch (type) {
        case "id":
          return value.replace(/(\d{4})\d{11}(\w{3})/, "$1***********$2");
        case "name":
          return value.replace(/.(?=.)/g, "*");
        case "phone":
          return value.replace(/(\d{3})\d{4}(\d{4})/, "$1****$2");
        case "addZero":
          return value ? value + " 0时" : value;
        default:
          return value;
      }
    }
  },
  watch: {
    value: {
      handler(val) {
        if (!this.filterType && this.mask) {
          this.currentValue = mask.toPattern(val, this.mask);
        } else {
          this.currentValue = val;
        }
      },
      immediate: true
    },
    currentValue(val) {
      this.$emit("input", val);
    }
  },
  methods: {
    inputValue(e) {
      let val = e.target.value;
      if (this.type === "idCard") val = val.toLocaleUpperCase();
      if (this.mask)
        return (this.currentValue = mask.toPattern(val, this.mask));
      this.currentValue = val;
    },
    blur() {
      this.$emit("blur");
    },
    eventClick(e) {
      this.$emit("click", e);
    },
    focusInput() {
      // this.$refs.input.scrollIntoView({ block: "start", behavior: "smooth" });
      // this.$emit("focus")
    },
    // 邮箱自动完成
    focusEmail() {
      // return
      // this.$refs.emailInput.focus();
      // let range = document.createRange();
      // range.selectNodeContents(this.$refs.emailInput);
      // range.collapse(false);
      // let sel = window.getSelection();
      // sel.removeAllRanges();
      // sel.addRange(range);
    },
    emailFocusInput() {
      // this.emailPlaceholder = ''
    },
    noWrap(e) {
      if (e.which == 13) {
        e.cancelBubble = true;
        e.stopPropagation();
        e.preventDefault();
        this.completeEmail();
      }
    },
    autoComplete() {
      let val = this.$refs.emailInput.innerText;
      this.currentValue = val;
      // this.$refs.emailInput.innerText = this.currentValue
      // console.log(this.currentValue);
      let emailArr = this.emailComplete;
      this.emailPlaceholder = "";
      console.log(this.emailPlaceholder);
      if (val.length > 0) {
        this.autoCompleteText = "@qq.com";
        if (val.indexOf("@") > -1) {
          let index = val.indexOf("@");
          if (val.length == index + 1) {
            this.autoCompleteText = "qq.com";
          } else {
            let rule = val.slice(index + 1);
            let item = emailArr.find(v => {
              if (v.emailEnd.indexOf(rule) == 0) {
                return v;
              }
            });
            if (item) {
              this.autoCompleteText = item.emailEnd.slice(rule.length);
            } else {
              this.autoCompleteText = "";
            }
          }
        }
      } else {
        this.autoCompleteText = "";
        this.emailPlaceholder = this.placeholder;
      }
    },
    completeEmail() {
      this.currentValue = this.currentValue + this.autoCompleteText;
      this.currentValue = this.currentValue.substring(0, this.maxlength);
      this.$refs.emailInput.innerText = this.currentValue;
      this.autoCompleteText = "";
    }
  },
  mounted() {
    if (this.email) {
      this.$refs.emailInput.innerText = this.currentValue;
    }
  },
  created() {
    if (this.value) {
      if (!this.filterType && this.mask) {
        this.currentValue = mask.toPattern(this.value, this.mask);
      } else {
        this.currentValue = this.value;
      }
    }
    this.emailPlaceholder = this._placeholder;
  }
};
</script>

<style lang="less" scoped>
@fontSize: 4vw;
@color: #303133;
@errorColor:#ed5539;
.e-input-com {
  position: relative;
  color: @color;
  .e-input-view {
    width: 100%;
    max-width: 100vw;
    overflow: hidden;
    min-height: 1.5em;
    font-size: @fontSize;
    padding: var(--padding);
    display: flex;
    box-sizing: content-box;
    .e-inpit-title {
      width: 6em;
      text-align: left;
      flex: none;
    }
    .e-input {
      flex: 1;
      text-align: var(--align);
      padding: 0 0 0 10%;
      box-sizing: border-box;
      outline: none;
      border: none;
      &::placeholder {
        color: var(--color);
      }
      &.div {
        padding-right: 3.5vw;
        position: relative;
        &::after {
          content: "";
          width: 2vw;
          height: 2vw;
          position: absolute;
          top: 50%;
          right: 1vw;
          transform-origin: center center;
          transform: translateY(-50%) rotate(45deg);
          border-right: 0.2vw solid var(--borderColor);
          border-top: 0.2vw solid var(--borderColor);
        }
        &::before {
          content: attr(data-placeholder);
          color: var(--color);
          font-size: @fontSize;
        }
      }
      &:focus {
        outline: none;
        border: none;
      }
    }
    .e-input-error {
      color: @errorColor;
    }
    &::after {
      content: "";
      height: 1px;
      display: block;
      position: absolute;
      width: 100%;
      background-color: #efefef;
      bottom: 0;
      left: 0;
    }
  }
  .email_div {
    padding: 0 !important;
    max-width: 100%;
    overflow: hidden;
    position: relative;
    line-height: 1.5em;
    outline: none;
    color: @color;
    box-sizing: border-box;
    white-space: nowrap;
    margin: 0 0 0 4vw;
    &::before {
      content: attr(data-placeholder);
      color: #bec1c8;
    }
    &::after {
      content: attr(data-tip);
      color: #bec1c8;
    }
  }
}
.error-input-com {
  .e-input-view {
    .e-inpit-title {
      color:@errorColor;
    }
    &::after {
      content: "";
      background-color: @errorColor;
    }
  }
}
</style>
