export default {
  value: [String, Number],
  type: {
    type: String,
    default: "text"
  },
  maxlength: {
    type: Number,
    default: 30
  },
  placeholder: {
    type: String,
    default: "请输入"
  },
  placeholderColor: { type: String, default: "#4ca2fe" },
  className: String,
  mask: String,
  title: String,
  readonly: Boolean,
  disabled: Boolean,
  check: [Boolean, Function],
  textAlign: {
    type: String,
    default: "right"
  },
  padding: {
    type: String,
    default: "4vw 0 4.5vw"
  },
  email: Boolean,
  isLink:Boolean,
  emailComplete: {
    type: Array,
    default: () => [
      {
        emailEnd: "qq.com",
        emailEndShow: true
      },
      {
        emailEnd: "163.com",
        emailEndShow: true
      },
      {
        emailEnd: "126.com",
        emailEndShow: true
      },
      {
        emailEnd: "sina.com",
        emailEndShow: true
      },
      {
        emailEnd: "139.com",
        emailEndShow: true
      },
      {
        emailEnd: "gmail.com",
        emailEndShow: true
      }
    ]
  },
  toTop:{
    type:Boolean,
    default:false
  },
  filterType:String,
  error:{
    type:Boolean,
    default:false
  }
}